/* Colors */
/* UTILIDADES */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: normal 1.4rem/2 "Roboto", sans-serif;
  background-color: #EBE7DE;
}
@media (min-width: 768px) {
  body {
    font: normal 1.6rem/2 "Roboto", sans-serif;
  }
}

h1,
h2,
.titulo-blanco,
h3,
h4,
h5,
h6 {
  margin: 0;
  text-transform: uppercase;
}

h1 {
  font: 900 5rem/1.5 "Roboto", sans-serif;
}

h2, .titulo-blanco {
  font: 900 4rem/1.5 "Roboto", sans-serif;
  text-align: center;
  color: #5E311E;
}
h2::after, .titulo-blanco::after {
  content: "";
  width: 10rem;
  height: 10rem;
  margin-inline: auto;
  margin-bottom: 3rem;
  display: block;
  background-image: url("../img/cafe.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
h2 span, .titulo-blanco span {
  display: block;
  font-size: 2rem;
  color: #C39C63;
}

.titulo-blanco {
  color: #ffffff;
}
.titulo-blanco::after {
  display: none;
}
.titulo-blanco span {
  color: #ffffff;
}

h3 {
  font: 900 3rem/1.5 "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

[class*=__container] {
  width: min(95%, 120rem);
  margin-inline: auto;
}

.header {
  position: relative;
  padding-block: 2rem;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.header__barra {
  position: relative;
  z-index: 1;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 15rem;
  }
}
.header__logo {
  width: 15rem;
  height: auto;
}
.header__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
}
@media (min-width: 768px) {
  .header__menu {
    flex-direction: row;
    gap: 2rem;
  }
}
.header__link {
  text-transform: uppercase;
  font-weight: 700;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header__link:hover {
    color: #C39C63;
  }
}
.header__link--select {
  color: #C39C63;
}
.header__titulo {
  position: relative;
  z-index: 1;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}

.header--inicio {
  background-image: url("../img/header_inicio.jpg");
}

.header--nosotros {
  background-image: url("../img/header_nosotros.jpg");
}

.header--proceso {
  background-image: url("../img/header_proceso.jpg");
}

.header--menu {
  background-image: url("../img/header_menu.jpg");
}

.header--galeria {
  background-image: url("../img/header_galeria.jpg");
}

.header--contacto {
  background-image: url("../img/header_contacto.jpg");
}

.main {
  position: relative;
  padding: 2rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main {
    margin-top: -15rem;
    padding: 10rem;
  }
}

.sobre-nosotros {
  margin-bottom: 5rem;
  text-align: center;
}

.iconos {
  margin-bottom: 5rem;
  display: grid;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .iconos {
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos__icono {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: center;
}
.iconos__img {
  width: 10rem;
  height: auto;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .iconos__img {
    width: 15rem;
  }
}
.iconos__texto {
  color: #5E311E;
  font-weight: bold;
}

.experiencia__galeria {
  display: grid;
  gap: 1rem;
}
@media (min-width: 768px) {
  .experiencia__galeria {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .experiencia__galeria {
    grid-template-columns: repeat(4, 1fr);
  }
}
.experiencia__picture {
  width: 100%;
  height: 20rem;
}
@media (min-width: 1024px) {
  .experiencia__picture:nth-child(1) {
    grid-row: 1/3;
    height: 100%;
  }
  .experiencia__picture:nth-child(2) {
    grid-column: 2/4;
  }
  .experiencia__picture:nth-child(4) {
    grid-column: 2/3;
    grid-row: 2/4;
    height: 100%;
  }
  .experiencia__picture:nth-child(5) {
    grid-column: 3/5;
  }
  .experiencia__picture:nth-child(6) {
    grid-column: 3/5;
  }
  .experiencia__picture:nth-child(7) {
    grid-column: 1/2;
    grid-row: 3/5;
    height: 100%;
  }
  .experiencia__picture:nth-child(8) {
    grid-column: 2/5;
  }
  .experiencia__picture:nth-child(9) {
    grid-column: 1/3;
  }
  .experiencia__picture:nth-child(10) {
    grid-column: 3/5;
  }
}
.experiencia__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.menu-carta {
  padding-block: 10rem;
  background-color: #5E311E;
  background-image: url("../img/menu_arriba.svg"), url("../img/menu_abajo.svg");
  background-repeat: no-repeat;
  background-position: center 2%, center 99%;
  background-size: 100%;
}
@media (min-width: 768px) {
  .menu-carta {
    padding-block: 20rem;
    padding-bottom: 30rem;
    background-position: center 2%, center 85%;
  }
}
@media (min-width: 1024px) {
  .menu-carta {
    background-size: max(70rem, 45%);
  }
}
.menu-carta__grid {
  margin-block: 2rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
@media (min-width: 768px) {
  .menu-carta__grid {
    margin-block: 5rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.menu-carta__titulo {
  text-align: center;
  color: #ffffff;
}
.menu-carta__lista {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.menu-carta__lista p {
  color: #ffffff;
  line-height: 1.5;
}
.menu-carta__lista span {
  display: block;
  color: #C39C63;
}
.menu-carta__iten {
  display: grid;
  grid-template-columns: 1fr 5rem;
  align-items: center;
  gap: 0.8rem;
}
.menu-carta__precio {
  font-size: 2rem;
  font-weight: bold;
  color: #ffffff;
}
@media (min-width: 768px) {
  .menu-carta__precio {
    font-size: 2.5rem;
  }
}

.testimoniales {
  padding-block: 2rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .testimoniales {
    margin-top: -10rem;
    padding-block: 10rem;
  }
}
.testimoniales__testimonial {
  width: min(90%, 50rem);
  margin-inline: auto;
  padding: 2rem;
  border-radius: 1.6rem;
  background-color: #EBE7DE;
}
.testimoniales__texto {
  text-align: center;
}
.testimoniales__autor {
  text-align: right;
  font-weight: bold;
  color: #5E311E;
}

.footer {
  padding-bottom: 2rem;
  background-color: #5E311E;
}
.footer__grid {
  padding: 2rem;
  display: grid;
  gap: 2rem;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__heading {
  text-transform: uppercase;
  font-weight: 700;
  color: #C39C63;
}
.footer__copy {
  margin-top: 1rem;
  text-align: center;
  color: #ffffff;
}

.nosotros__content {
  display: grid;
  justify-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .nosotros__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros__img {
  width: 100%;
  height: auto;
  box-shadow: 5px 5px 1rem rgba(0, 0, 0, 0.6);
}
@media (min-width: 768px) {
  .nosotros__img {
    width: 50rem;
  }
}
.nosotros__block {
  padding-left: 2rem;
  border-left: 0.5rem solid #5E311E;
}

.proceso__content {
  display: grid;
  gap: 3.2rem;
}
@media (min-width: 768px) {
  .proceso__content {
    gap: 5rem;
  }
}
.proceso__proceso {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .proceso__proceso {
    flex-direction: row;
    align-items: center;
    gap: 3.2rem;
  }
  .proceso__proceso:nth-child(even) {
    flex-direction: row-reverse;
  }
  .proceso__proceso picture {
    flex: 1;
  }
  .proceso__proceso div {
    flex: 1;
  }
}
.proceso__titulo {
  color: #5E311E;
  display: inline-block;
}
.proceso__titulo::after {
  margin-bottom: 1rem;
  content: "";
  display: block;
  width: 80%;
  height: 0.2rem;
  background-color: #C39C63;
}
.proceso__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.menu {
  background-image: url("../img/cafe.jpg");
  background-size: 28rem;
  background-position: center bottom;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .menu {
    background-position: right bottom;
  }
}
.menu__content {
  display: grid;
  max-width: 40rem;
  margin-inline: auto;
  grid-template-rows: repeat(2, 1fr);
  gap: 10rem;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__content {
    max-width: unset;
    grid-template-rows: unset;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    text-align: left;
  }
}

.main-bottom-0 {
  padding-bottom: 0;
}

@media (min-width: 768px) {
  .contacto {
    background-image: url("../img/granos_cafe.jpg");
    background-position: right bottom;
    background-repeat: no-repeat;
    background-size: 45%;
  }
}
@media (min-width: 1024px) {
  .contacto {
    background-size: 30%;
  }
}
@media (min-width: 768px) {
  .contacto__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}

.formulario {
  max-width: 40rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .formulario {
    max-width: unset;
    margin-inline: unset;
  }
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario__input {
  width: 100%;
  padding: 0.5rem;
  border: 3px solid #5E311E;
  outline: none;
}
.formulario__buttom {
  width: 100%;
  padding: 1rem;
  border: none;
  color: #ffffff;
  background-color: #5E311E;
  cursor: pointer;
}
.formulario__buttom:hover {
  background-color: #ab5937;
}